html {
  font-size: 10px; }

body {
  font-family: 'Lato', sans-serif;
  font-size: 20px; }

* {
  box-sizing: border-box; }

.wrapper {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  padding: 4rem; }

.table-data {
  border: 1px solid #ccc;
  border-collapse: collapse;
  margin: 2rem;
  width: 100%; }
  .table-data th {
    border: 1px solid #ccc;
    padding: 1rem 2rem; }
    .table-data th:first-child {
      width: 5%; }
    .table-data th:nth-child(2) {
      width: 35%; }
    .table-data th:nth-child(n+3) {
      width: 20%; }
  .table-data td {
    padding: 1rem;
    border: 1px solid #ccc;
    text-align: center; }
  .table-data .active {
    background-color: #8DD35F; }

.b-search {
  display: inline-block;
  position: relative;
  color: #ccc;
  width: 25rem; }
  .b-search input {
    width: 100%;
    border-radius: 99999rem;
    padding: .5rem 2rem .5rem 2rem;
    border: 2px solid #ccc; }
    .b-search input:focus {
      outline: none;
      border-color: #8DD35F;
      color: #8DD35F; }
      .b-search input:focus + .icon {
        color: #8DD35F; }
      .b-search input:focus::placeholder {
        color: #8DD35F; }
  .b-search .icon {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 3rem; }

.btn-get {
  padding: 1rem 2rem;
  background-color: #f5f5f5;
  border: 2px solid #ccc;
  border-radius: 1rem;
  font-weight: bold;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.8);
  transition: all .4s; }
  .btn-get:hover {
    color: #8DD35F;
    border-color: #8DD35F; }
  .btn-get:active {
    box-shadow: none; }
